	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wednesday","date":"26 Apr 2000","slug":"/2000-04-26-wednesday/","tags":"Y2000"},"html":"<p>Brent and Sheila may have the Mariners, but we're just down the road from the <a href=\"http://www.geocities.com/Colosseum/Sideline/4800/\">Dohren Wild Farmers</a></p>\n<p><a href=\"http://andrea.editthispage.com/\">Andrea</a>:  \"Panama makes me think of <a href=\"http://titan.informatik.uni-bonn.de/~schumach/pages/janosch.html\">Janosch</a>.\" We have a Janosch calender in Christopher's (future) room. I'd never heard of Janosch before coming here, though. </p>\n<p>Both Mama and I have some learning to about children's books from the other's country. We're trying to already estalish a going-to-bed routine for Christopher with 15 minutes of reading. Yesterday Mama tried reading English nursery rhymes... but, of course, they're all new to her, and she doesn't know the tunes. (This evening I cheated... I read Christopher a chapter from <a href=\"http://www.oreilly.com/catalog/dbnation/\">Database Nation</a>.)</p>\n<p><a href=\"http://andrea.editthispage.com\">Andrea</a> sent in <a href=\"http://shanson.editthispage.com/discuss/msgReader$219\">a couple additions</a> to the <a href=\"http://shanson.editthispage.com/stories/storyReader$125\">German EditThisPage / Manila Sites</a> list</p>"},"next":{"fields":{"title":"Easter Sunday","date":"23 Apr 2000","slug":"/2000-04-23-easter-sunday/"}},"previous":{"fields":{"title":"Father and Son Virus","date":"29 Apr 2000","slug":"/2000-04-29-father-and-son-virus/"}}},{"node":{"fields":{"title":"Easter Sunday","date":"23 Apr 2000","slug":"/2000-04-23-easter-sunday/","tags":"Y2000"},"html":"<p>My brother, who doesn't even own a computer, sent me this dubious electronic <a href=\"http://www.americangreetings.com/pickup.pd?i=49792032&m=7093&rr=y\">Easter card</a>.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/04/20000423018.jpg\" height=\"262\" width=\"350\" border=\"0\" alt=\"crh0423.jpg: \" /></p>\n<p>A smiley picture from this morning. He must be smiling because of the link from <a href=\"http://scriptingnews.userland.com/backissues/2000/04/23\">Scripting News</a></p>\n<p>Lots of new pictures at PhotoPoint: <a href=\"http://albums.photopoint.com/j/AlbumIndex?u=185392&a=5038363\">1</a> <a href=\"http://albums.photopoint.com/j/AlbumIndex?u=185392&a=5039996\">2</a> <a href=\"http://albums.photopoint.com/j/AlbumIndex?u=185392&a=5040043\">3</a> <a href=\"http://albums.photopoint.com/j/AlbumIndex?u=185392&a=5038348\">4</a>, count 'em, 4 albums!</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/04/20000423019.jpg\" height=\"262\" width=\"350\" border=\"0\" alt=\"cows0423.jpg: \" /></p>\n<p>The cows are back! Our neighbor Achim brought out his Charolais cattle yesterday. Their first stop is the pasture past the horse shed behind our house. They'll stay about a week before they move on. </p>\n<p>The Easter Bonfire, on the night before Easter, is the big event of the year in Lüllau. It's organized by the volunteer fire department. They gather the wood, set the fire and sell bratwurst, beer and schnaps. I'd say 300 people were there last night. </p>\n<p>(Most of the little towns in this area have bonfires for Easter, but it doesn't seem to be a universal German tradition. Mama had never heard of it when she was growing up.)</p>\n<p>Christopher started expressing displeasure at 9pm, so we went home earlier than we had planned.</p>"},"next":{"fields":{"title":"Waking up","date":"19 Apr 2000","slug":"/2000-04-19-waking-up/"}},"previous":{"fields":{"title":"Wednesday","date":"26 Apr 2000","slug":"/2000-04-26-wednesday/"}}},{"node":{"fields":{"title":"Waking up","date":"19 Apr 2000","slug":"/2000-04-19-waking-up/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/04/crh0419.jpg\" height=\"414\" width=\"350\" border=\"0\" alt=\"crh0419.jpg: \" /></p>\n<p>Here is Christopher just waking up this morning. (April 23: Christopher says, \"Thanks for the <a href=\"http://scriptingnews.userland.com/backissues/2000/04/23\">link</a>, Dave. I'll take that as a compliment!\") Points of note:</p>\n<ul>\n<li>He's wearing his Oshkosh locomotive pyjamas, from John and Diane\n</li>\n<li>He's kicked the covers off, as usual\n</li>\n<li>He's not smiling, because he sees the camera\n</li>\n<li>He's lying crooked, with feet slightly to the left. He`d turn completely to the left if he could.\n</li>\n<li>Be thankful that <a href=\"http://www.digiscents.com\">DigiScents</a> is not activated for this page</li>\n</ul>\n<p><a href=\"http://Mama.editthispage.com\">Mama</a> has a picture of a <a href=\"http://Mama.editthispage.com/2000/04/19\">complete left turn</a>. He just slides under the side of the gym.</p>"},"next":{"fields":{"title":"Back to the old routine","date":"17 Apr 2000","slug":"/2000-04-17-back-to-the-old-routine/"}},"previous":{"fields":{"title":"Easter Sunday","date":"23 Apr 2000","slug":"/2000-04-23-easter-sunday/"}}}],"pathPrefix":"","first":false,"last":false,"index":1066,"pageCount":1101}}

